import React, { useCallback, useEffect, useLayoutEffect } from "react";
import { Link, Navigate } from "react-router-dom";
import Messages from "../../components/Messages/Messages.component";

import "./chat_page.css";

import useUserCridentials from "../../hooks/useUserAccessData";
import useHandleMessages from "../../hooks/handleMessages";
import SendMessage from "../../components/SendMessage/SendMessage.conponent";
import { checkAccesToken } from "../../app/user/user.thunks";

const ChatPage = () => {
  const { signOut } = useUserCridentials();

  const { validateToken, isLogged, dispatch } = useUserCridentials();

  // useLayoutEffect(() => {
  //   validateToken();
  // });

  return !isLogged ? (
    <Navigate to={"/signup"} replace />
  ) : (
    <div className="messages_container">
      <Link
        to="/login"
        onClick={signOut}
        style={{ position: "absolute", top: 0, left: 0 }}
      >
        Log out
      </Link>

      <Messages />
      <SendMessage />
    </div>
  );
};

export default ChatPage;
